<template>
  <div class="update-gender">
      <van-picker
        show-toolbar
        :columns="columns"
        :default-index='defaultIndex'
        @confirm="onConfirm"
        @cancel="$emit('close')"
        @change="onGenderChange"
        />
  </div>
</template>

<script>
import { updateUserProfile } from '../../api/user'
export default {
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      columns: ['', ''],
      defaultIndex: this.value
    }
  },
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '',
        forbidclick: true // 
      })
      // 
      try {
        await updateUserProfile({
          gender: this.defaultIndex
        })

        this.$emit('input', this.defaultIndex)
        this.$toast.success('')
        this.$emit('close')
      } catch (error) {
        this.$toast.fail('')
      }
    },
    onGenderChange (picker, value, index) {
      this.defaultIndex = index
    }
  }
}
</script>

